<template>
  <div class="search">
    <!---->
    <SearchNav :showSearchPanel="showSearchPanel"/>

    <!---->
    <div class="shop">
      <!---->
      <div class="menu-wrapper">
        <ul>
          <!--<li class="menu-item current">
            <span></span>
          </li>-->
          <li class="menu-item" v-for="(goods, index) in searchGoods" :key="index"
              :class="{current: index===currentIndex}" @click="clickLeftItem(index)" ref="menuList">
            <span>{{goods.name}}</span>
          </li>
        </ul>
      </div>

      <!---->
      <div class="shop-wrapper">
        <ul ref="shopParent">
          <li class="shop-li" v-for="(goods, index1) in searchGoods" :key="index1">
            <div class="shop-title">
              <!--<h4></h4>-->
              <h4>{{goods.name}}</h4>
              <a href="">></a>
            </div>
            <ul class="phone-type" v-if="goods.tag==='phone'">
              <li v-for="(phone, index3) in goods.category" :key="index3">
                <img :src="phone.icon" alt="">
              </li>
            </ul>
            <ul class="shop-items">
              <!--<li>
                <img src="./images/s1.png" alt="">
                <span></span>
              </li>-->
              <li v-for="(item, index2) in goods.items" :key="index2">
                <img :src="item.icon" alt="">
                <span>{{item.title}}</span>
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </div>

    <!---->
    <search-panel v-if="isShow" :showSearchPanel="showSearchPanel"/>
  </div>
</template>

<script>
import SearchNav from "./children/SearchNav";
import SearchPanel from "./children/SearchPanel";
import {mapState} from "vuex";
import BScroll from 'better-scroll';

export default {
  name: "Search",
  data() {
    return {
      scrollY: 0,  //Y()
      rightLiTops: [],  //
      isShow: false,  //
    }
  },
  components: {
    SearchNav,
    SearchPanel
  },
  mounted() {
    /*this.$store.dispatch('reqSearchGoods', ()=>{
      this.$nextTick(()=>{
        //
        this._initBScroll();
      })
    })*/
    this.$store.dispatch('reqSearchGoods')

  },
  computed: {
    ...mapState(['searchGoods']),

    currentIndex(){  //
      //1.
      const {scrollY, rightLiTops}= this;
      //2.
      let cIndex= rightLiTops.findIndex((item, index)=>{
        this._leftScroll(index);
        return scrollY >= item && scrollY < rightLiTops[index+1];
      })

      return cIndex
    }
  },
  watch: {
    searchGoods(){
      this.$nextTick(()=>{
        //1.
        this._initBScroll();

        //2.
        this._initRightLiTops();
      })
    }
  },
  methods: {
    //
    _initBScroll(){
      //
      this.leftBScroll= new BScroll('.menu-wrapper', {
        scrollY: true, //
        click: true  //
      })

      //
      this.rightBScroll= new BScroll('.shop-wrapper', {
        scrollY: true, //
        probeType: 3  //, probeType: 3
      })
      // console.log(this.rightBScroll)

      //
      this.rightBScroll.on('scroll', (pos)=>{
        // console.log(pos)
        // scrollY
        this.scrollY = Math.abs(Math.round(pos.y))
        // this.scrollY= Math.abs(pos.y)

      })
    },

    //
    _initRightLiTops(){
      //
      const tempArr= [];

      //
      let top= 0;
      tempArr.push(top);

      //shop-lili, 
      let shopLis= this.$refs.shopParent.getElementsByClassName('shop-li');
      Array.prototype.slice.call(shopLis).forEach((li, index)=>{  //, 
        // console.log(li, index)

        // li
        if(index===shopLis.length-1){
          // console.log(li, index)
          //lipadding-bottom = --60-50
          li.style.paddingBottom= `${window.innerHeight-li.clientHeight-110}px`
        }

        top += li.clientHeight;  //li
        // console.log(top)
        tempArr.push(top);
      })

      //
      this.rightLiTops= tempArr;
      // console.log(this.rightLiTops)
    },

    //, 
    _leftScroll(index){
      let menuList= this.$refs['menuList'];
      // console.log(menuList)
      let el= menuList[index];
      // console.log(el)
      this.leftBScroll.scrollToElement(el,300, 0, -300);
    },

    //, 
    clickLeftItem(index){
       this.scrollY= this.rightLiTops[index];
       this.rightBScroll.scrollTo(0, -this.scrollY, 300);
    },

    //
    showSearchPanel(flag){
      this.isShow = flag;
    }
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  @import "../../common/stylus/mixins.styl"
  .search
    width 100%
    height 100%
    background-color #f5f5f5

  .shop
    width 100%
    position absolute
    top 60px
    bottom 50px
    background-color #fff
    display flex
    flex-direction row
    overflow hidden
    .menu-wrapper
      //width 20%
      //height 100%
      width 80px
      flex 0 1 80px
      background-color #e0e0e0
      .menu-item
        width 100%
        height 60px
        background-color #fafafa
        line-height 60px
        color #666
        font-weight lighter
        position relative
        display flex
        justify-content center
        align-items center
      .current
        color #e02e24
      .current::before
        content ''
        background-color #e02e24
        width 4px
        height 30px
        position absolute
        left 0
    .shop-wrapper
      //width 80%
      //height 100%
      flex 1
      background-color #fff
      .shop-title
        height 44px
        padding 0 10px
        color #999
        display flex
        flex-direction row
        justify-content space-between
        align-items center
        a
          text-decoration none
          color #999
          font-weight lighter
      .shop-items
        display flex
        flex-wrap wrap
        li
          width 33.3%
          height 90px
          display flex
          flex-direction column
          justify-content center
          align-items center
          color #666height
          font-weight lighter
          font-size 14px
          img
            width 60%
            height 60%
            margin-bottom 5px
      .phone-type
        width 100%
        display flex
        flex-direction row
        flex-wrap wrap
        border-bottom-1px(#ccc)
        li
          width 33.3%
          //background-color red
          margin 5px 0
          display flex
          justify-content center
          align-items center
          img
            width 70%
</style>



